*{
       box-sizing: border-box;
}
body{
       margin: 0;
       height: 100vh;
       background-color: #0b2434;
       padding: 2px;
       font-family: 'Karla',sans-serif;
       display: flex;
       justify-content: center;
       align-items: center;
}
main{
       background-color: #f5f5f5;
       height: 400px;
       max-width: 800px;
       border-radius: 5px;
       padding: 20px;
       display: flex;
       flex-direction: column;
       justify-content: space-around;
       align-items: center;
       margin:0 auto;
}
.title{
       font-size: 40px;
       margin: 0;
}
.instructions {
       font-family: 'Inter', sans-serif;
       font-weight: 400;
       margin-top: 0;
       text-align: center;
   }
   
.dice-container {
       display: grid;
       grid-template: auto auto / repeat(5, 1fr);
       grid-gap: 20px;
       gap: 20px;
       margin-bottom: 40px;
}
   
.die-face {
       height: 50px;
       width: 50px;
       box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
       border-radius: 10px;
       display: flex;
       justify-content: center;
       align-items: center;
       cursor: pointer;
}
   
.die-num {
       font-size: 2rem;
}

.roll-dice {
       height: 50px;
       width: 150px;
       border: none;
       border-radius: 6px;
       background-color: #5035FF;
       color: white;
       font-size: 1.2rem;
       font-family: 'Karla', sans-serif;
       cursor: pointer;
}
.roll-dice:hover{
       background-color: #2e10f0b4;
}

.roll-dice:focus {
       outline: none;
}

.roll-dice:active {
       box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
@media (max-width:800px){
       main{
              max-width: 100%;
              font-size: 1.39rem;
       }
       .roll-dice{
              font-size: 1.3rem;
              padding: .5em;
              height: 110px;
              width: 140px;
       }
       .die-num{
              font-size: 2.4rem;
       }
       .die-face{
              height: 60px;
              width: 60px;
       }
}
